<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/dashboard' }"></el-breadcrumb-item>
      <el-breadcrumb-item>goInception</el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <div align="">
        <p> <code>error_level</code> </p>
        <ul>
          <li><code>2</code> </li>
          <li><code>1</code> <code>ignore_warnings</code></li>
          <li><code>0</code> </li>
        </ul>
      </div>

      <el-table :data="tableData" style="width: 100%" border stripe>
        <el-table-column label="#" type="index" align="center" header-align="center"></el-table-column>
        <el-table-column label="" prop="Desc" align="center" header-align="center"></el-table-column>
        <el-table-column label="" prop="Name" align="center" header-align="center"></el-table-column>
        <el-table-column label="" align="center" header-align="center">
          <template slot-scope="scope">
            <el-input v-model="scope.row.Value" placeholder=""></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" header-align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="inception_set(scope.row.Name,scope.row.Value)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

  </div>

</template>

<script>
export default {
  data() {
    return {
      tableData: [],

    };
  },
  mounted() {
    this.get_inception_show_levels();
  },
  methods: {
    async get_inception_show_levels() {
      const {data: res} = await this.$ajax
          .get(`/get_inception_show_levels/`)
          .catch(() => {
            return this.$notify.error({
              title: "",
              message: ""
            });
          });
      if (res.msg != "success") return this.$message.error("");
      this.tableData = res.data;
    },
    async inception_set(Name, Value) {
      const {data: res} = await this.$ajax
          .post(`/set_inception_show_levels/`, {"Name": Name, "Value": Value})
          .catch(() => {
            return this.$notify.error({
              title: "",
              message: ""
            });
          });
      if (res.msg != "success") return this.$message.error("");
      this.$message({
        message: '',
        type: 'success'
      });
      await this.get_inception_show_levels();

    }
  }
}
</script>

